<template>
  <div>
    <div v-if="!showEdit && !showAdd" class="container">
      <div class="header">
        <div class="title">用户列表</div>
        <el-button style="margin-left: 30px" @click="showAdd=!showAdd">添加用户</el-button>
      </div>
      <ceres-table
        :pagination="pagination"
        :table-column="tableColumn"
        :table-data="tableData"
        :operate="operate"
        @handleEdit="handleEdit"
        @handleDelete="handleDelete"
        @currentChange = "currentChange"
      />
    </div>

    <user-add v-if="showAdd" @hanleHide="handleHideAdd"/>
    <user-edit v-if="showEdit" :edit-i-d="editID" @hanleHide="handleHideEdit"/>
  </div>
</template>

<script type="text/ecmascript-6">
import UserModel from '@/models/user'
import UserAdd from './UserAdd'
import UserEdit from './UserEdit'

export default {
  name: 'CompanyList',
  components: {
    UserAdd,
    UserEdit
  },
  data() {
    return {
      tableColumn: [
        { prop: 'username', label: '用户名' },
        { prop: 'realname', label: '真实姓名' },
        { prop: 'company_name', label: '公司' },
        { prop: 'mobile', label: '联系方式' },
        { prop: 'email', label: '邮箱' }
      ],
      tableData: [],
      operate: [
        { name: '编辑', func: 'handleEdit', type: 'primary' },
        { name: '删除', func: 'handleDelete', type: 'danger' },
      ],
      showEdit: false,
      showAdd: false,
      pagination: {
        pageSize: 0,
        pageTotal: 0
      },
      editID: '',
      user: {},
    }
  },
  computed: {
    company_id() {
      return this.$store.state.user.user.company_id
    }
  },
  created() { },
  mounted() {
    this.getUserList(1, 10)
  },
  methods: {
    async getUserList(page, size) {
      const res = await UserModel.getList(this.company_id, size, page)
      this.tableData = [...res.items]
      this.pagination.pageTotal = res.total
      this.pagination.pageSize = res.size
    },
    handleEdit(val) {
      this.showEdit = true
      this.editID = val.row.id
    },
    async handleDelete(val) {
      await UserModel.deleteOne(val.row.id)
      this.$message.success('删除成功')
      this.getUserList(1, 10)
    },
    handleHideAdd() {
      this.showAdd = false
      this.getUserList(1, 10)
    },
    handleHideEdit() {
      this.showEdit = false
      this.getUserList(1, 10)
    },
    // 切换当前页
    currentChange(page) {
      this.getUserList(page, 10)
    }
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .container {
    .header {
      display flex
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
    }
  }
</style>
